@font-face {
  font-family: 'corebiz-icons';
  src: url('data:application/octet-stream;base64,') format('woff'),
       url('data:application/octet-stream;base64,') format('truetype');
}
  
[class^="icon-"]:before, [class*=" icon-"]:before {
  font-family: 'corebiz-icons';
  font-style: normal;
  font-weight: normal;

  display: inline-block;
  text-decoration: inherit;
  font-size: 15px;
  margin: 0;
  color: #000;
  text-align: center;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
  
.icon-vtex:before       { content: '\e800'; }
.icon-user:before       { content: '\e801'; }
.icon-search:before     { content: '\e802'; }
.icon-prev-arrow:before { content: '\e803'; }
.icon-next-arrow:before { content: '\e804'; }
.icon-mini-cart:before  { content: '\e805'; }
.icon-menu:before       { content: '\e806'; }
.icon-headphones:before { content: '\e807'; }
.icon-email:before      { content: '\e808'; }
.icon-cross:before      { content: '\e809'; }
.icon-corebiz:before    { content: '\e80a'; }